.go-to-container {
    display: flex;
    justify-content: flex-end;
    position: relative;
    margin-bottom: 10px;
    z-index: 2;
}

.go-to {
    border-bottom: 1px solid rgb(255, 255, 255);
    border-top: 1px solid rgb(255, 255, 255);
    font-size: 1.1rem;
    padding: 3px;
    width: fit-content;
}

.line {
    border-bottom: 1px solid rgb(255, 255, 255);
    width: 30px;
}

.go-to p {
    background-color: rgba(249, 195, 103, 0.3);
    color: rgb(46, 12, 70);
    font-size: .95rem;
    /* font-style: normal; */
    font-weight: 600;
    padding: 2px 5px;
}